'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, Suspense } from 'react';
import { useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { VibeSettingsBlock, isAvailableVibeForSettings } from '@/features/vibe-settings/index.js';

import { WithClassName, useStore } from '@/shared/lib/index.js';

import styles from './VibeContext.module.scss';

interface Props extends WithClassName {
    context: string | null;
    onResetClick: () => void;
}

export const VibeContext = observer(({ context, onResetClick, className }: Props): ReactElement => {
    const { vibe } = useStore();
    const { formatMessage } = useIntl();

    return (
        <div className={clsx(styles.root, className)}>
            <Suspense>{isAvailableVibeForSettings(vibe.meta?.seeds) && <VibeSettingsBlock />}</Suspense>
            {context && (
                <div className={clsx(styles.context)}>
                    <Caption variant="span" type="controls" size="m" weight="medium" lineClamp={1}>
                        {context}
                    </Caption>
                    <Button
                        className={styles.resetButton}
                        variant="text"
                        radius="round"
                        withRipple={false}
                        icon={<Icon variant="close" size="xxs" />}
                        onClick={onResetClick}
                        aria-label={`${formatMessage({ id: 'vibe-actions.reset-settings' })} ${context}`}
                    />
                </div>
            )}
        </div>
    );
});
