'use client';

import { observer } from 'mobx-react-lite';
import { ReactElement, use, useCallback, useEffect, useMemo, useState } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Popover, PopoverContent } from '@yandex-music-int/sol/components/Popover';
import { Tooltip } from '@yandex-music-int/sol/components/Tooltip';
import { Caption, Heading } from '@yandex-music-int/sol/components/Typography';

import {
    LayoutName,
    LoadingState,
    useLayout,
    usePlayableVibeContext,
    useStore,
    useAnalyticsBlock,
    useAnalyticsPage
} from '@/shared/lib/index.js';

import { useContentHeight } from '../../lib/useContentHeight.js';
import { ContextItemModel } from '../../model/ContextItem.js';
import { RestrictonValueModel } from '../../model/RestrictionValue.js';
import { RestrictionBlock } from '../RestrictionBlock/RestrictionBlock.js';
import { ContextItemButton } from '../RestrictionButton/ContextItemButton.js';
import { RestrictionButton } from '../RestrictionButton/RestrictionButton.js';

import styles from './VibeSettingsBlock.module.scss';

const OFFSET_X_VALUE = 20;
const OFFSET_Y_VALUE = 7;
const SETTINGS_PADDING = 16;

const TRANSITION_OPTIONS = {
    duration: 300,
    initial: {
        transform: 'translateX(5%)',
        opacity: 0
    },
    open: {
        transform: 'translateX(0)',
        opacity: 1
    },
    close: {
        transform: 'translateX(5%)',
        opacity: 0
    }
};

export const VibeSettingsBlock = observer((): ReactElement => {
    const { formatMessage } = useIntl();
    const { vibe, vibeSettings, settings } = useStore();
    const { pageId } = useAnalyticsPage();
    const { blockId } = useAnalyticsBlock();
    const { resetSettings } = usePlayableVibeContext({
        seeds: vibe.meta?.seeds,
        pageIdForFrom: pageId,
        blockIdForFrom: blockId
    });
    const { contentRef } = useLayout();
    const { contentHeight } = useContentHeight(0);

    const [open, setOpen] = useState(false);

    if (vibeSettings.loadingState === LoadingState.IDLE) {
        use(vibeSettings.getData());
    }

    const closeSettingsHandler = useCallback(() => {
        setOpen(false);
    }, [setOpen]);

    const isMobile = settings.layout === LayoutName.Mobile;

    const style = useMemo(() => {
        if (isMobile) {
            return {
                height: '90vh'
            };
        }
        if (contentRef) {
            return {
                insetInlineEnd: OFFSET_X_VALUE,
                insetBlockStart: contentRef.offsetTop + OFFSET_Y_VALUE,
                height: contentHeight - SETTINGS_PADDING
            };
        }
        return {};
    }, [contentHeight, contentRef, isMobile]);

    useEffect(() => {
        return () => {
            vibeSettings.reset();
        };
    }, [vibeSettings]);

    return (
        <Popover
            placement="bottom"
            isMobile={isMobile}
            strategy="fixed"
            transform={false}
            positionElement={contentRef}
            disableFloatingStyles={!isMobile}
            open={open}
            onOpenChange={setOpen}
            style={style}
            overlayClassName={styles.overlay}
            transition={TRANSITION_OPTIONS}
        >
            {vibe.meta?.context ? (
                <Button
                    className={styles.toggleSettingsButton}
                    size="m"
                    radius="round"
                    icon={<Icon variant="filter" size="xs" />}
                />
            ) : (
                <Button
                    className={styles.toggleSettingsButton}
                    size="default"
                    radius="xxxl"
                    icon={<Icon variant="filter" size="xxs" />}
                >
                    <Caption variant="span" type="controls" size="m" weight="medium">
                        <FormattedMessage id="interface-actions.configure-my-vibe" />
                    </Caption>
                </Button>
            )}
            <PopoverContent className={styles.popover}>
                <div className={styles.root}>
                    <div className={styles.header}>
                        <Heading variant="h3" size="s" weight="bold">
                            <FormattedMessage id="interface-actions.my-vibe-settings" />
                        </Heading>
                        <div className={styles.actions}>
                            <Tooltip
                                offsetOptions={4}
                                placement="left"
                                text={formatMessage({ id: 'interface-actions.reset-my-vibe-settings' })}
                            >
                                <Button
                                    variant="outline"
                                    radius="round"
                                    size="xxxs"
                                    icon={<Icon variant="reset" size="xxxs" />}
                                    onClick={resetSettings}
                                    disabled={vibe.isLoading || vibe.isMyVibe}
                                    aria-label={formatMessage({ id: 'interface-actions.reset-my-vibe-settings' })}
                                />
                            </Tooltip>
                            <Button
                                radius="round"
                                color="secondary"
                                size="xxs"
                                icon={<Icon variant="close" size="xxs" />}
                                onClick={closeSettingsHandler}
                                aria-label={formatMessage({ id: 'interface-actions.close-my-vibe-settings' })}
                            />
                        </div>
                    </div>
                    <div className={styles.settings}>
                        {Boolean(vibeSettings.contextItems.length > 0) && (
                            <RestrictionBlock
                                title={formatMessage({ id: 'interface-actions.my-vibe-context-settings' })}
                                restrictionsClassName={styles.contextItems}
                            >
                                {vibeSettings.contextItems.map((item: ContextItemModel) => (
                                    <ContextItemButton item={item} key={item.seed} />
                                ))}
                            </RestrictionBlock>
                        )}
                        {vibeSettings.diversity && (
                            <RestrictionBlock
                                title={vibeSettings.diversity.title}
                                restrictionsClassName={styles.diversity}
                            >
                                {vibeSettings.diversity.values.map((restrictionValue: RestrictonValueModel) => (
                                    <RestrictionButton
                                        type={vibeSettings.diversity.type}
                                        restrictionValue={restrictionValue}
                                        key={restrictionValue.value}
                                    />
                                ))}
                            </RestrictionBlock>
                        )}
                        {vibeSettings.moodEnergy && (
                            <RestrictionBlock
                                title={vibeSettings.moodEnergy.title}
                                restrictionsClassName={styles.moodEnergy}
                            >
                                {vibeSettings.moodEnergy.values.map((restrictionValue: RestrictonValueModel) => (
                                    <RestrictionButton
                                        type={vibeSettings.moodEnergy.type}
                                        restrictionValue={restrictionValue}
                                        key={restrictionValue.value}
                                    />
                                ))}
                            </RestrictionBlock>
                        )}
                        {vibeSettings.language && (
                            <RestrictionBlock
                                title={vibeSettings.language.title}
                                restrictionsClassName={styles.contextItems}
                            >
                                {vibeSettings.language.values.map((restrictionValue: RestrictonValueModel) => (
                                    <RestrictionButton
                                        type={vibeSettings.language.type}
                                        restrictionValue={restrictionValue}
                                        key={restrictionValue.value}
                                    />
                                ))}
                            </RestrictionBlock>
                        )}
                    </div>
                </div>
            </PopoverContent>
        </Popover>
    );
});
