import { Station } from '@yandex-music-int/resource-collection';

import { ContextItemModel } from '../model/ContextItem.js';

export const contextItemResponseToModel = (stationResponse: Station): ContextItemModel => {
    const { type, tag } = stationResponse.id;
    return {
        title: stationResponse.name,
        seed: `${type}:${tag}`,
        specialContext: stationResponse.specialContext,
        specialContextImage: stationResponse.specialContextImage
    };
};
