import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { MouseEvent, ReactElement, useCallback, useMemo } from 'react';
import { FormattedMessage } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';

import { WithClassName, useStore } from '@/shared/lib/index.js';

import styles from './PlayButton.module.scss';

interface Props extends WithClassName {
    isPlaying: boolean;
    onClick: () => void;
}

export const PlayButton = observer(({ isPlaying, onClick, className }: Props): ReactElement => {
    const { vibe } = useStore();
    const iconVariant = useMemo(() => {
        return isPlaying ? 'pauseVibe' : 'playVibe';
    }, [isPlaying]);

    const handleOnClick = useCallback(
        (event: MouseEvent<HTMLButtonElement>) => {
            onClick?.();
            event.stopPropagation();
            event.preventDefault();
        },
        [onClick]
    );

    return (
        <Button
            className={clsx(
                styles.root,
                {
                    [styles.root_isLoading]: vibe.isLoading
                },
                className
            )}
            withRipple={false}
            withHover={false}
            onClick={handleOnClick}
            variant="text"
            icon={<Icon size="s" className={clsx(styles.icon, styles[`icon_${iconVariant}`])} variant={iconVariant} />}
            disabled={vibe.isLoading}
        >
            <FormattedMessage id="entity-names.my-vibe" />
        </Button>
    );
});
