export const vertexShader = `
    void main () {
        gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
`;

export const fragmentShader = `
    precision highp float;
    precision highp int;
    
    uniform float uTime;
    uniform vec2 uScreenSize;
    uniform float uBpm;
    uniform float uHue;
    uniform vec3 uBackgroundColor;
    uniform float uMid;
    uniform float uLow;
    
    const float MAX_TEMPO = 174.0;
    const float YZ_ROTATION_MULTIPLIER = 1.0;
    const float XZ_ROTATION_MULTIPLIER = 0.7;
    const float XY_ROTATION_MULTIPLIER = 0.5;
    const float WAVE_MOVEMENT_MULTIPLIER = 1.5;
    const float FREQUENCY = 1.2;
    vec2 vReactionPoint = vec2(0.0, 0.0);
    vec3 vReactionColor =  vec3(1.0, 1.0, 1.0);
    const float REACTION_POINT_SIZE =  0.2;
    const float MIN_FREQUENCY =  0.5;
    const float FREQUENCY_MID_MULTIPLIER =  0.5;
    const float vReaction =  .0;
    const float FREQUENCY_REACTION_MULTIPLIER =  1.5;
    const float SCENE_ROTATION =  3.1451 * 1.75;
    const float SCENE_SCALE =  1.0;
    const float vReactionScale =  0.8;
    const float DISTANCE =  3.5;
    const int PASS_COUNT =  4;
    const float COLOR_MULTIPLIER =  7.0;
    
    float bpm(float b) {
        return b / MAX_TEMPO;
    }
    
    vec3 hsl2rgb(vec3 c) {
        return c.z + c.y * (c.z + c.y * ((c.z + c.y * (clamp( abs(mod(c.x*6.0+vec3(0.0,4.0,2.0),6.0)-3.0)-1.0, 0.0, 1.0 )-0.5)*(1.0-abs(2.0*c.z-1.0)))-0.5)*(1.0-abs(2.0*c.z-1.0))-0.5)*(1.0-abs(2.0*c.z-1.0));
    }
    
    mat2 mRotate(float angle) {
        float c = cos(angle);
        float s = sin(angle);
        
        return mat2(c, -s, s, c);
    }
    float waves(vec3 point, float freq) {
        point.yz *= mRotate(uTime * YZ_ROTATION_MULTIPLIER);
        point.xz *= mRotate(uTime * XZ_ROTATION_MULTIPLIER);
        point.xy *= mRotate(uTime * XY_ROTATION_MULTIPLIER);

        vec3 q = point * (1.0 + bpm(uBpm)) + uTime * WAVE_MOVEMENT_MULTIPLIER;
        float centeredCircle = length(point);
        float centerCut = log(length(point) - 1.0);
        float waves = freq * sin(FREQUENCY * q.x - sin(FREQUENCY * q.y + sin(q.z * FREQUENCY))) - 1.0;
        
        return centeredCircle * centerCut + waves;
    }
    void main(){
        float TAIL_INTENSITY =  3.0 + 1.0 * uLow;
        vec2 uvTrue = gl_FragCoord.xy / uScreenSize;
        vec2 uv = -1.0 + 2.0 * uvTrue;

        float minSide = min(uScreenSize.x, uScreenSize.y);
        vec2 minSquare = vec2(minSide, minSide);
        vec2 aspectSides = uScreenSize / minSide;
        uv *= aspectSides;

        vec2 reactPoint = vReactionPoint * aspectSides;
        float reactIntensity = clamp(length(uv - reactPoint) / 2.0 / REACTION_POINT_SIZE, 0.0, 1.0);

        float freq = MIN_FREQUENCY + uMid * FREQUENCY_MID_MULTIPLIER;
        freq = mix(freq + vReaction * FREQUENCY_REACTION_MULTIPLIER, freq, reactIntensity);

        uv *= mRotate(SCENE_ROTATION);
        uv *= 1.0 / SCENE_SCALE;
        uv /= vReactionScale;

        float opacity = 0.0;
        float d = DISTANCE;
        for(int i = 0; i < PASS_COUNT; i++) {
            vec3 p = vec3(0, 0, 5.) + vec3(uv, -1.0) * d;
            float result = waves(p, freq);
            float f = result - waves(p + 0.03, freq);
            float l = f * (COLOR_MULTIPLIER + vReaction * 2.0);
            opacity = opacity * l + smoothstep(TAIL_INTENSITY, 1.0, result) * l;
            d += min(result, 1.0);
        }

        vec3 gradColor = mix(hsl2rgb(vec3(uHue, 1.0, 0.5)), hsl2rgb(vec3(uHue + 0.06, 1.0, 0.5)), uvTrue.y);
        vec3 reactedColor = mix(gradColor, vReactionColor, vReaction * (1.0 - reactIntensity));
        vec3 color = mix(uBackgroundColor, reactedColor, clamp(opacity, 0.0, 1.0));

        float average_color = (color.x + color.y + color.z) / 3.0;
        float result_opacity = min(1.0, (average_color - 0.0) * 5.0);
        gl_FragColor = vec4(color, result_opacity);
    }
`;
