'use client';

import { useEffect, useState } from 'react';

import { useLayout } from '@/shared/lib/index.js';

interface UseContentHeight {
    contentHeight: number;
}

export const useContentHeight = (initialHeight: number = 0): UseContentHeight => {
    const [contentHeight, setContentHeight] = useState(initialHeight);
    const { contentRef } = useLayout();

    useEffect(() => {
        const contentObserver = new ResizeObserver((entries: ResizeObserverEntry[]) => {
            const [entry] = entries || [];
            if (entry && entry.contentRect.height !== contentHeight) {
                setContentHeight(entry.contentRect.height);
            }
        });

        if (contentRef) {
            contentObserver.observe(contentRef);
        }

        return () => {
            contentObserver.disconnect();
        };
    }, [contentHeight, contentRef]);

    return {
        contentHeight
    };
};
