import { AVAILABLE_VIBES_FOR_SETTINGS, DEFAULT_VIBE_STATION_ID } from '@/shared/lib/index.js';

export const isAvailableVibeForSettings = (seeds?: string[]): boolean => {
    if (seeds) {
        return seeds.some((seed: string) => {
            const [stationType] = seed?.split(':') || [];
            return (
                seed === DEFAULT_VIBE_STATION_ID || (stationType && AVAILABLE_VIBES_FOR_SETTINGS.includes(stationType))
            );
        });
    }
    return false;
};
