import { Instance, types } from 'mobx-state-tree';

import { DEFAULT_VIBE_STATION_ID } from '@/shared/lib/index.js';

export const Vibe = types
    .model('Vibe', {
        title: types.string,
        stationId: types.string,
        seeds: types.array(types.string),
        idForFrom: types.maybe(types.string),
        imageUrl: types.maybe(types.string),
        animationUrl: types.maybe(types.string),
        colors: types.maybe(
            types.model({
                average: types.maybe(types.string),
                waveText: types.maybe(types.string)
            })
        )
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => {
        return {
            get context(): string | null {
                if (self.seeds) {
                    if (self.stationId === DEFAULT_VIBE_STATION_ID) {
                        if (self.seeds.length > 1) {
                            return self.title;
                        }
                    } else {
                        return self.title;
                    }
                }
                return null;
            }
        };
    });

export type VibeModel = Instance<typeof Vibe>;
