import { cast } from 'mobx-state-tree';

import { EnumValue, LocalizableEnumValueEntry } from '@yandex-music-int/resource-collection';

import { RestrictonModel } from '../model/Restriction.js';
import { RestrictonValueModel } from '../model/RestrictionValue.js';

import { restrictionValueResponseToModel } from './restrictionValueResponseToModel.js';
import { RestrictonType } from './types/RestrictionType.js';

export const restrictionResponseToModel = (type: RestrictonType, restrictionResponse: EnumValue): RestrictonModel => {
    const values: RestrictonValueModel[] = [];
    restrictionResponse.possibleValues.forEach((value: LocalizableEnumValueEntry) => {
        if (value.unspecified) {
            return;
        }
        values.push(restrictionValueResponseToModel(value));
    });
    return {
        type,
        title: restrictionResponse.name,
        values: cast(values)
    };
};
