'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, use, useCallback, useEffect } from 'react';

import {
    LoadingState,
    useAnalyticsBlock,
    useAnalyticsPage,
    usePlayableVibeContext,
    useStore,
    WithClassName,
    Experiments
} from '@/shared/lib/index.js';

import { MyVibeAnimation } from '../MyVibeAnimation/MyVibeAnimation.js';

import styles from './MyVibe.module.scss';
import { PlayButton } from './PlayButton/PlayButton.js';
import { VibeContext } from './VibeContext/VibeContext.js';

interface Props extends WithClassName {}

export const MyVibe = observer(({ className }: Props): ReactElement => {
    const { vibe, experiments } = useStore();
    const { pageId } = useAnalyticsPage();
    const { blockId } = useAnalyticsBlock();

    const { isVibeContext, isPlaying, togglePlay, resetSettings } = usePlayableVibeContext({
        seeds: vibe.meta?.seeds,
        pageIdForFrom: pageId,
        blockIdForFrom: blockId
    });

    const isVibeSettingsDisabled = experiments.checkExperiment(Experiments.WebNextDisableVibeSettings, 'on');

    if (vibe.getLastLoadingState === LoadingState.IDLE) {
        use(vibe.getLastVibe());
    }

    const onResetClick = useCallback(() => {
        if (isVibeContext) {
            resetSettings();
        } else {
            if (vibe.vibeResetLoadingState !== LoadingState.PENDING) {
                vibe.vibeReset();
            }
        }
    }, [isVibeContext, vibe, resetSettings]);

    useEffect(() => {
        return () => {
            vibe.reset();
        };
    }, [vibe]);

    return (
        <div className={clsx(styles.root, className)}>
            <MyVibeAnimation isPlaying={isPlaying} className={styles.animation} />
            <div className={styles.controls}>
                <PlayButton isPlaying={isPlaying} onClick={togglePlay} />
                {!isVibeSettingsDisabled && (
                    <VibeContext
                        className={styles.settingsButton}
                        context={vibe.meta?.context}
                        onResetClick={onResetClick}
                    />
                )}
            </div>
        </div>
    );
});
