'use client';

import { observer } from 'mobx-react-lite';
import { ReactElement, useEffect, useRef } from 'react';

import { useIntersectionObserver } from '@yandex-music-int/sol/common/hooks/useIntersectionObserver.js';

import { Theme, useStore, useLayout, WithClassName } from '@/shared/lib/index.js';

import { RenderState, VibeAnimationRenderer } from '../../lib/VibeAnimationRenderer/index.js';

import styles from './MyVibeAnimation.module.scss';

type Noop = (() => void) | void;

interface Props extends WithClassName {
    isPlaying: boolean;
}

export const MyVibeAnimation = observer(({ isPlaying, className }: Props): ReactElement => {
    const canvas = useRef<HTMLCanvasElement | null>(null);
    const renderer = useRef<VibeAnimationRenderer | null>(null);
    const isVisible = useIntersectionObserver(canvas);
    const { theme, colorScheme } = useLayout();
    const { sonataState } = useStore();

    useEffect((): Noop => {
        if (canvas.current) {
            const { bpm, hue, energy } = sonataState.entityMeta?.trackParametrs || {};
            const vibeAnimationRenderer = new VibeAnimationRenderer(canvas.current);
            renderer.current = vibeAnimationRenderer;
            renderer.current?.applySettings(bpm, hue, energy);

            canvas.current?.classList.add(styles.root_visible);

            return () => {
                vibeAnimationRenderer.destroy();
            };
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect((): Noop => {
        if (renderer.current) {
            const isDark = theme === Theme.System ? colorScheme === Theme.Dark : theme === Theme.Dark;

            renderer.current.applyTheme(isDark);
        }
    }, [theme, colorScheme]);

    useEffect((): Noop => {
        if (renderer.current) {
            const { bpm, hue, energy } = sonataState.entityMeta?.trackParametrs || {};
            if (isPlaying) {
                renderer.current?.playAnimation(bpm, hue, energy);
            } else {
                renderer.current?.idleAnimation();
            }
        }
    }, [isPlaying, sonataState.entityMeta]);

    useEffect((): Noop => {
        if (renderer.current) {
            if (isVisible) {
                renderer.current?.enableRender(RenderState.BLOCK_VISIBILITY);
            } else {
                renderer.current?.disableRender(RenderState.BLOCK_VISIBILITY);
            }
        }
    }, [isVisible]);

    return <canvas className={className} ref={canvas} />;
});
