'use client';

import { observer } from 'mobx-react-lite';
import { ReactElement, use, useEffect, useMemo, CSSProperties } from 'react';
import { useIntl } from 'react-intl';

import { LandingSkeletonType } from '@yandex-music-int/resource-collection';
import { ScrollableContent } from '@yandex-music-int/sol/components/ScrollableContent';
import { Tooltip, TooltipContent } from '@yandex-music-int/sol/components/Tooltip';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { Skeleton } from '@/widgets/landing/index.js';
import { MyVibe } from '@/widgets/my-vibe/index.js';
import { SomethingWentWrong } from '@/widgets/something-went-wrong/index.js';

import {
    LoadingState,
    useStore,
    useRumPage,
    AnalyticsPageProvider,
    AnalyticsPage,
    AnalyticsBlockProvider,
    AnalyticsBlock,
    Experiments,
    getVersion
} from '@/shared/lib/index.js';
import { Footer } from '@/shared/ui/index.js';

import styles from './Main.module.scss';

const PRELOAD_BLOCKS_COUNT = 2;

export const MainPage = observer((): ReactElement => {
    const { main, user, experiments } = useStore();
    const { formatMessage } = useIntl();
    const version = getVersion();

    if (main.landing.loadingState === LoadingState.IDLE) {
        use(
            main.landing.getSkeleton(
                {
                    id: LandingSkeletonType.WEB_MAIN,
                    showWizard: user.settings.showWizard
                },
                {
                    preloadBlocks: PRELOAD_BLOCKS_COUNT
                }
            )
        );
    }

    useEffect(() => () => main.landing.reset(), [main.landing]);

    useRumPage(main.landing.loadingState === LoadingState.RESOLVE);

    const isVibeDisabled = experiments.checkExperiment(Experiments.WebNextDisableVibe, 'on');

    const landingBlockStyle = useMemo(() => {
        if (isVibeDisabled) {
            return {
                'margin-block-start': '1vh'
            };
        }

        return {};
    }, [isVibeDisabled]);

    const beta = useMemo(() => {
        if (process.env.BUILD_TYPE === 'desktop') {
            return (
                <Tooltip placement="right" offsetOptions={4}>
                    <Caption
                        aria-label={`Beta ${formatMessage({ id: 'desktop.app-version' }, { version })}`}
                        variant="div"
                        type="text"
                        size="s"
                        weight="medium"
                        className={styles.beta}
                    >
                        Beta
                    </Caption>
                    <TooltipContent>{version}</TooltipContent>
                </Tooltip>
            );
        }
        return (
            <Caption variant="div" type="text" size="s" weight="medium" className={styles.beta}>
                Beta
            </Caption>
        );
    }, [version, formatMessage]);

    return (
        <AnalyticsPageProvider pageId={AnalyticsPage.HOME}>
            <ScrollableContent className={styles.root} containerClassName={styles.content}>
                {beta}
                <div>
                    {!isVibeDisabled && (
                        <AnalyticsBlockProvider blockId={AnalyticsBlock.RUP_MAIN}>
                            <MyVibe className={styles.myVibe} />
                        </AnalyticsBlockProvider>
                    )}

                    <div className={styles.landing} style={landingBlockStyle as CSSProperties}>
                        <Skeleton
                            landing={main.landing}
                            errorComponent={<SomethingWentWrong className={styles.error} />}
                        />
                    </div>
                </div>

                <Footer className={styles.footer} />
            </ScrollableContent>
        </AnalyticsPageProvider>
    );
});
