import { Instance, cast, flow, getEnv, types } from 'mobx-state-tree';

import {
    RotorResourceWaveLastResetResponse,
    RotorResourceWaveLastResetStatus,
    RotorResourceWaveLastResponse,
    Wave
} from '@yandex-music-int/resource-collection';

import { Vibe, waveResponseToModel } from '@/entities/vibe/index.js';

import { DEFAULT_VIBE_STATION_ID, LoadingState, StoreEnv } from '@/shared/lib/index.js';

export const VibeActions = types
    .model('VibeActions', {
        meta: types.maybeNull(Vibe),
        applyingSetting: types.maybeNull(types.string),
        isLoading: types.boolean,
        getLastLoadingState: types.enumeration(Object.values(LoadingState)),
        vibeResetLoadingState: types.enumeration(Object.values(LoadingState))
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getLastVibe: flow(function* (): Generator<
                Promise<RotorResourceWaveLastResponse>,
                void,
                RotorResourceWaveLastResponse
            > {
                const { rotorResource } = getEnv<StoreEnv>(self);

                if (self.getLastLoadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.getLastLoadingState = LoadingState.PENDING;

                    const data = yield rotorResource.waveLast();

                    self.meta = waveResponseToModel(data);

                    self.getLastLoadingState = LoadingState.RESOLVE;
                } catch (error) {
                    // eslint-disable-next-line no-console
                    console.error(error);

                    self.getLastLoadingState = LoadingState.REJECT;
                }
            }),
            vibeReset: flow(function* (): Generator<
                Promise<RotorResourceWaveLastResetResponse>,
                void,
                RotorResourceWaveLastResetResponse
            > {
                const { rotorResource } = getEnv<StoreEnv>(self);

                if (self.vibeResetLoadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.vibeResetLoadingState = LoadingState.PENDING;

                    const response = yield rotorResource.waveLastReset();

                    if (response === RotorResourceWaveLastResetStatus.OK) {
                        self.meta = waveResponseToModel({
                            name: '',
                            seeds: [DEFAULT_VIBE_STATION_ID],
                            stationId: DEFAULT_VIBE_STATION_ID,
                            idForFrom: DEFAULT_VIBE_STATION_ID
                        });
                    }

                    self.vibeResetLoadingState = LoadingState.RESOLVE;
                } catch (error) {
                    // eslint-disable-next-line no-console
                    console.error(error);

                    self.vibeResetLoadingState = LoadingState.REJECT;
                }
            }),
            setApplyingSetting(seed?: string) {
                if (seed) {
                    self.applyingSetting = cast(seed);
                } else {
                    self.applyingSetting = null;
                }
            },
            setIsLoading(state: boolean) {
                self.isLoading = state;
            },
            setVibe(wave: Wave) {
                self.meta = waveResponseToModel(wave);
            },
            reset() {
                self.meta = null;
                self.vibeResetLoadingState = LoadingState.IDLE;
                self.getLastLoadingState = LoadingState.IDLE;
            }
        };
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isMyVibe() {
            return self.meta && self.meta.seeds.length === 1 && self.meta.seeds[0] === DEFAULT_VIBE_STATION_ID;
        }
    }));

export type VibeActionsModel = Instance<typeof VibeActions>;
