import { cast } from 'mobx-state-tree';

import { Wave } from '@yandex-music-int/resource-collection';

import { VibeModel } from '../model/Vibe.js';

export const waveResponseToModel = (response: Wave): VibeModel => {
    return cast({
        title: response.name,
        seeds: response.seeds,
        stationId: response.stationId,
        idForFrom: response.idForFrom
    });
};
