import { Mesh, PlaneGeometry, WebGLRenderer, Scene, OrthographicCamera, ShaderMaterial, Vector2, Vector3 } from 'three';

import { SPEED } from './constants.js';
import { vertexShader, fragmentShader } from './shader.js';
import { Ticker } from './ticker.js';

export enum RenderState {
    ACTIVE_TAB = 'ACTIVE_TAB',
    BLOCK_VISIBILITY = 'BLOCK_VISIBILITY',
    MANUAL = 'MANUAL'
}

const SCALE_RATIO = 0.95;
const MAX_FPS = 25;

export class VibeAnimationRenderer {
    private ticker: Ticker;
    private scene: Scene = new Scene();
    private camera: OrthographicCamera = new OrthographicCamera();
    private shader: ShaderMaterial | null = null;
    private renderer: WebGLRenderer | null = null;
    private speed: number = SPEED.IDLE;
    private renderState: Record<RenderState, boolean> = {
        [RenderState.ACTIVE_TAB]: true,
        [RenderState.BLOCK_VISIBILITY]: true,
        [RenderState.MANUAL]: true
    };

    public constructor(private readonly canvas: HTMLCanvasElement) {
        this.renderer = new WebGLRenderer({
            canvas,
            antialias: false,
            alpha: false
        });

        this.camera.position.set(0, 0, 10);

        this.handleOnResize = this.handleOnResize.bind(this);
        this.handleOnVisibilityChange = this.handleOnVisibilityChange.bind(this);

        this.ticker = new Ticker(MAX_FPS, this.render.bind(this));

        this.create();
        this.handleOnResize();
        this.setupListeners();
    }

    private get width(): number {
        return window.innerWidth * SCALE_RATIO;
    }

    private get height(): number {
        return window.innerHeight * SCALE_RATIO;
    }

    private setupListeners(): void {
        window.addEventListener('resize', this.handleOnResize);
        document.addEventListener('visibilitychange', this.handleOnVisibilityChange);
    }

    private create(): void {
        this.shader = new ShaderMaterial({
            uniforms: {
                uTime: { value: 0.0 },
                uScreenSize: { value: new Vector2(this.width, this.height) },
                uBpm: { value: 120.0 },
                uHue: { value: 0.0638 },
                uBackgroundColor: { value: new Vector3(0, 0, 0) },
                uMid: { value: 0.0 },
                uLow: { value: 1.0 }
            },
            vertexShader,
            fragmentShader
        });
        const geometry = new PlaneGeometry(2, 2);

        const plane = new Mesh(geometry, this.shader);
        plane.position.z = -1;

        this.scene.add(plane);
    }

    private render(): void {
        if (this.shader?.uniforms?.uTime) {
            this.shader.uniforms.uTime.value += this.speed;
        }

        this.renderer?.render(this.scene, this.camera);
    }

    private handleOnResize(): void {
        this.renderer?.setSize(this.width, this.height);

        this.camera.updateProjectionMatrix();

        if (this.shader?.uniforms?.uScreenSize) {
            this.shader.uniforms.uScreenSize.value.x = this.width;
            this.shader.uniforms.uScreenSize.value.y = this.height;
        }
    }

    private handleOnVisibilityChange(): void {
        if (document.hidden) {
            this.disableRender(RenderState.ACTIVE_TAB);
        } else {
            this.enableRender(RenderState.ACTIVE_TAB);
        }
    }

    private updateRenderingFlag(): void {
        const isRendering = Object.values(this.renderState).every((state: boolean) => state);

        if (isRendering) {
            this.ticker.start();
        } else {
            this.ticker.stop();
        }
    }

    public applyTheme(isDarkTheme: boolean): void {
        // Это цвет токена темной или светлой темы, который сконвертирован в интервал [0, 1]
        // Например, rgb(18, 18, 18) === [0.0705, 0.0705, 0.0705]
        const color = isDarkTheme ? 0 : 0.9607;

        if (this.shader?.uniforms?.uBackgroundColor) {
            this.shader.uniforms.uBackgroundColor.value = new Vector3(color, color, color);
        }
    }

    public applySettings(bpm?: number, hue?: number, energy?: number): void {
        if (bpm && this.shader?.uniforms.uBpm) {
            this.shader.uniforms.uBpm.value = bpm;
        }

        if (hue && this.shader?.uniforms.uHue) {
            this.shader.uniforms.uHue.value = hue;
        }

        this.speed = energy ?? SPEED.IDLE;
    }

    public playAnimation(bpm?: number, hue?: number, energy?: number): void {
        this.applySettings(bpm, hue, energy ?? SPEED.ACTIVE);
    }

    public idleAnimation(): void {
        this.speed = SPEED.IDLE;
    }

    public enableRender(type: RenderState): void {
        this.renderState[type] = true;
        this.updateRenderingFlag();
    }

    public disableRender(type: RenderState): void {
        this.renderState[type] = false;
        this.updateRenderingFlag();
    }

    public destroy(): void {
        if (this.handleOnResize) {
            window.removeEventListener('resize', this.handleOnResize);
        }

        if (this.handleOnVisibilityChange) {
            document.removeEventListener('visibilitychange', this.handleOnVisibilityChange);
        }
    }
}
