import { useCallback, useMemo } from 'react';
import { useIntl } from 'react-intl';

import { AnalyticsBlock, AnalyticsPage, usePlayableVibeContext, useStore } from '@/shared/lib/index.js';

interface UseApplySettingParams {
    seed: string;
    title: string;
    isStation?: boolean;
}

interface UseApplySetting {
    isApplying: boolean;
    isSelected: boolean;
    isLoading: boolean;
    ariaLabel: string;
    onClickHandler: () => void;
}

export const useApplySetting = ({ seed, title, isStation }: UseApplySettingParams): UseApplySetting => {
    const { vibe } = useStore();
    const { applySetting } = usePlayableVibeContext({
        seeds: vibe.meta?.seeds,
        pageIdForFrom: AnalyticsPage.HOME,
        blockIdForFrom: AnalyticsBlock.RUP_MAIN
    });

    const { formatMessage } = useIntl();

    const isSelected = vibe.meta?.seeds.includes(seed);

    const isApplying = vibe.applyingSetting === seed;

    const ariaLabel = useMemo(() => {
        if (isSelected) {
            return `${formatMessage({ id: 'vibe-actions.remove' })} ${title}`;
        }
        return `${formatMessage({ id: 'vibe-actions.apply' })} ${title}`;
    }, [isSelected, title, formatMessage]);

    const onClickHandler = useCallback(async () => {
        vibe.setApplyingSetting(seed);
        await applySetting(seed, isStation);
        vibe.setApplyingSetting();
    }, [applySetting, isStation, seed, vibe]);

    return {
        isApplying,
        isSelected,
        isLoading: vibe.isLoading,
        ariaLabel,
        onClickHandler
    };
};
