import { flow, getEnv, Instance, types, cast } from 'mobx-state-tree';

import {
    RotorResourceWaveSettingsParams,
    RotorResourceWaveSettingsResponse,
    WaveSettingsBlock
} from '@yandex-music-int/resource-collection';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';

import { contextItemResponseToModel } from '../lib/contextItemResponseToModel.js';
import { restrictionResponseToModel } from '../lib/restrictionResponseToModel.js';
import { RestrictonType } from '../lib/types/RestrictionType.js';

import { ContextItem } from './ContextItem.js';
import { Restricton } from './Restriction.js';

export const VibeSettings = types
    .model('VibeSettings', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        contextItems: types.array(ContextItem),
        diversity: types.maybeNull(Restricton),
        moodEnergy: types.maybeNull(Restricton),
        language: types.maybeNull(Restricton)
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getData: flow(function* (
                params?: RotorResourceWaveSettingsParams
            ): Generator<Promise<RotorResourceWaveSettingsResponse>, void, RotorResourceWaveSettingsResponse> {
                const { rotorResource } = getEnv<StoreEnv>(self);

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield rotorResource.waveSettings(params);

                    if (data?.settingRestrictions?.diversity) {
                        self.diversity = restrictionResponseToModel(
                            RestrictonType.DIVERSITY,
                            data.settingRestrictions.diversity
                        );
                    }

                    if (data?.settingRestrictions?.moodEnergy) {
                        self.moodEnergy = restrictionResponseToModel(
                            RestrictonType.MOOD_ENERGY,
                            data.settingRestrictions.moodEnergy
                        );
                    }

                    if (data?.settingRestrictions?.language) {
                        self.language = restrictionResponseToModel(
                            RestrictonType.LANGUAGE,
                            data.settingRestrictions.language
                        );
                    }

                    const contexts =
                        Array.isArray(data?.blocks) &&
                        data?.blocks?.find((block: WaveSettingsBlock) => block?.type === 'contexts');
                    if (contexts && Array.isArray(contexts.items)) {
                        self.contextItems = cast(contexts.items.map(contextItemResponseToModel));
                    }

                    // @ts-expect-error
                    // TS считает, что статусы никогда не пересекутся, но на самом деле мы можем уйти со страницы,
                    // раньше чем закончится последний ретрай и/или запрос, когда уже проставлен статус IDLE
                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.RESOLVE;
                    }
                } catch (error) {
                    // eslint-disable-next-line no-console
                    console.error(error);

                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.REJECT;
                    }
                }
            }),
            reset() {
                self.loadingState = LoadingState.IDLE;
                self.contextItems = cast([]);
                self.diversity = null;
                self.moodEnergy = null;
                self.language = null;
            }
        };
    });

export type VibeSettingsModel = Instance<typeof VibeSettings>;
