import { LocalizableEnumValueEntry } from '@yandex-music-int/resource-collection';

import { RestrictonValueModel } from '../model/RestrictionValue.js';

export const restrictionValueResponseToModel = (valueResponse: LocalizableEnumValueEntry): RestrictonValueModel => {
    return {
        value: valueResponse.value,
        title: valueResponse.name,
        seed: valueResponse.serializedSeed,
        imageUrl: valueResponse.imageUrl
    };
};
