import { Instance, types } from 'mobx-state-tree';

import { RestrictonType } from '../lib/types/RestrictionType.js';

import { RestrictonValue } from './RestrictionValue.js';

export const Restricton = types.model('Restricton', {
    type: types.enumeration(Object.values(RestrictonType)),
    title: types.string,
    values: types.array(RestrictonValue)
});

export type RestrictonModel = Instance<typeof Restricton>;
