'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo } from 'react';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { RestrictonType } from '../../lib/types/RestrictionType.js';
import { useApplySetting } from '../../lib/useApplySetting.js';
import { RestrictonValueModel } from '../../model/RestrictionValue.js';

import styles from './RestrictionButton.module.scss';

interface Props {
    type: RestrictonType;
    restrictionValue: RestrictonValueModel;
}

export const RestrictionButton = observer(({ type, restrictionValue }: Props): ReactElement => {
    const { isApplying, isSelected, isLoading, ariaLabel, onClickHandler } = useApplySetting({
        seed: restrictionValue.seed,
        title: restrictionValue.title
    });

    const title = useMemo(() => {
        return (
            <Caption variant="span" type="controls" size="m" weight="medium" className={styles.title}>
                {restrictionValue.title}
            </Caption>
        );
    }, [restrictionValue.title]);

    switch (type) {
        case RestrictonType.DIVERSITY:
            return (
                <Button
                    className={clsx(styles.button, styles.diversityButton, {
                        [styles.button_isSelected]: isSelected,
                        [styles.button_isApplying]: isApplying
                    })}
                    radius="xl"
                    onClick={onClickHandler}
                    disabled={isLoading}
                    aria-label={ariaLabel}
                >
                    <Image src={restrictionValue.imageUrl} fit="contain" width={40} height={40} />
                    {title}
                </Button>
            );
        case RestrictonType.MOOD_ENERGY:
            return (
                <div className={styles.moodEnergy}>
                    <Button
                        variant="text"
                        className={clsx(styles.button, styles.moodEnergyButton, {
                            [styles.button_isSelected]: isSelected,
                            [styles.button_isApplying]: isApplying
                        })}
                        radius="round"
                        withRipple={false}
                        onClick={onClickHandler}
                        disabled={isLoading}
                        aria-label={ariaLabel}
                    >
                        <Image
                            src={restrictionValue.imageUrl}
                            fit="contain"
                            width={52}
                            height={52}
                            alt={restrictionValue.title}
                        />
                    </Button>
                    {title}
                </div>
            );
        case RestrictonType.LANGUAGE:
            return (
                <Button
                    radius="xxxl"
                    className={clsx(styles.button, styles.textButton, {
                        [styles.button_isSelected]: isSelected,
                        [styles.button_isApplying]: isApplying
                    })}
                    onClick={onClickHandler}
                    disabled={isLoading}
                    aria-label={ariaLabel}
                >
                    {title}
                </Button>
            );
    }
});
